import { CartPage } from './../cart/cart';
import { SearchPage } from './../search/search';
import { VieworderdetailsPage } from './../vieworderdetails/vieworderdetails';
import { ApiProvider } from './../../providers/api/api';
import { Component, OnInit } from '@angular/core';
import { IonicPage, NavController, NavParams, ModalController } from 'ionic-angular';
import { ProductcategorydetailPage } from '../productcategorydetail/productcategorydetail';
import { ViewcartPage } from '../viewcart/viewcart';

/**
 * Generated class for the ProductcategoryPage page.
 *
 * See https://ionicframework.com/docs/components/#navigation for more info on
 * Ionic pages and navigation.
 */

@IonicPage()
@Component({
  selector: 'page-productcategory',
  templateUrl: 'productcategory.html',
})
export class ProductcategoryPage implements OnInit{

  productCategoryList: any = [];
  items = [];  
  productTitle:''
  // router: Router;


  constructor(public navCtrl: NavController,
              public navParams: NavParams,
              public apiProvider: ApiProvider,
              public modalCtrl: ModalController
              ) {

                this.getProductCategoriesApi();
  }

  ionViewDidLoad() {
    console.log('ionViewDidLoad VieworderPage');
    this.getProductCategoriesApi();
  }


  cartPage() {
    // let modal = this.modalCtrl.create(CartPage);
    // modal.present();

    this.navCtrl.push(ViewcartPage);
  }
  
     searchPage() {
  
    this.navCtrl.setRoot(SearchPage);
  }



  ngOnInit() {
    console.log('ngOnInit VieworderPage');
    this.getProductCategoriesApi();
  }

  doRefresh(event) {
    console.log('Begin async operation');
    this.getProductCategoriesApi();
    
    setTimeout(() => {
      console.log('Async operation has ended');
      //  event.target.complete();
      event.complete();
      // window.location.reload();
     // window.location.reload();
      // location.reload();
    }, 500);
  }



  getProductCategoriesApi(){
    console.log('getProductCategoriesApi called    ');
    const service = this.apiProvider.getProductCategories();
    service.subscribe((data) => {
   
        const resultado = data;
       // console.log('Get response: ' + resultado);
        this.productCategoryList = resultado;
        // console.log('Tushar called' + this.productCategoryList.length);

        this.productTitle = data.title;
       
    });
  }


  itemdetailPage(term_id,name) {


      
      

    this.navCtrl.push(ProductcategorydetailPage, {
      term_id: term_id,
      name:name
      
      // url: url,
      // status: status,
      // date:date,
      // modified:modified
      
    });


  
   
    console.log('Sent productsList id ' + term_id);
    // console.log('Sent productsList title ' + title);
  //  console.log('Sent productsList url ' + url);

  
  }


 

 
  
  

 

}




