  toggleAccordion() {
    if (this.accordionExpanded) {
      this.renderer.setElementStyle(
        this.cardContent.nativeElement,
        "max-height",
        "0px"
      );
      this.renderer.setElementStyle(
        this.cardContent.nativeElement,
        "padding",
        "0px 16px"
      );
    } else {
      this.renderer.setElementStyle(
        this.cardContent.nativeElement,
        "max-height",
        "500px"
      );
      this.renderer.setElementStyle(
        this.cardContent.nativeElement,
        "padding",
        "13px 16px"
      );
    }

    this.accordionExpanded = !this.accordionExpanded;
    this.icon = this.icon == "arrow-forward" ? "arrow-down" : "arrow-forward";
  }

