  async showLoadingControllerLaunch() {


    const { Network } = Plugins;
    this.networkListener = Network.addListener(
      'networkStatusChange',
      (status) => {
        console.log('Network status HomePage here', status);
        this.networkStatus = status;
      }
    );

    if ((await Network.getStatus()).connectionType === 'none') {
      this.showNetworkAlert();
      console.log('Network status not available', this.networkStatus);
    } else {
      this.networkStatus = await Network.getStatus();
      // this.showAlert();
      console.log('Network status available', this.networkStatus);
     // this.router.navigate(['/transactions']);
     this.presentLoadingDefault();
     this.showToastOnPageLoad();
    
    }



   
  }


  private async showNetworkAlert(): Promise<void> {
    // omitted;
    const alert = await this.alertController.create({
      title: 'Network Issues!',
      message: 'There are issues in network connectivity',

      buttons: [
        {
          text: 'Ok',
          handler: (ok) => {
            console.log('Confirm Ok');
            // resolve('ok');
          },
        },
        {
          text: 'Cancel',
          role: 'cancel',
          cssClass: 'secondary',
          handler: (cancel) => {
            console.log('Confirm Cancel');
            alert.dismiss();
            // resolve('cancel');
          },
        },
      ],
    });

    alert.present();
  }

  private async showCartRemovalAlert(id): Promise<void> {
    // omitted;
    const alert1 = this.alertController.create({
      title: 'Remove Item!',
      message: 'Do you want to remove items from cart!',
      enableBackdropDismiss: false,

      buttons: [
        {
          text: 'Ok',
          handler: (ok) => {
            console.log('Confirm Ok');
           
           
            const index = this.productsLocalCart.indexOf(id);
            this.productsLocalCart.splice(index, 1);
            localStorage.removeItem("products");

            this.showToastOnProductsRemovedCartLocally();
          },
        },
        {
          text: 'Cancel',
          role: 'cancel',
          cssClass: 'secondary',
          
          handler: (cancel) => {
            console.log('Confirm Cancel');
            alert1.dismiss();
            // resolve('cancel');
          },
        },
      ],
    });

    alert1.present();
  }
  presentLoadingDefault() {
    let loading = this.loadingController.create({
      content: 'Please wait Viewing Cart...'
    });
  
    loading.present();
  
    setTimeout(() => {
      loading.dismiss();
    }, 300);
  }

  async showToastOnPageLoad() 
