import { ViewcartPage } from './../viewcart/viewcart';
import { HttpClient } from '@angular/common/http';
import { CartPage } from './../cart/cart';
import { SearchPage } from './../search/search';
import { Component } from '@angular/core';
import { NavController, ModalController, NavParams, IonicPage, LoadingController } from 'ionic-angular';

/**
 * Generated class for the ProductcategorydetailPage page.
 *
 * See https://ionicframework.com/docs/components/#navigation for more info on
 * Ionic pages and navigation.
 */

@IonicPage()
@Component({
  selector: 'page-productcategorydetail',
  templateUrl: 'productcategorydetail.html',
})
export class ProductcategorydetailPage {

  obj;
  dynamicTermId:string;
  strId:string;
  productCategoryInformation: any = [];
  strData:string;
  strIdValue:string;
  strCommentStatus:string;
  strPingStatus:string;
  strProductCategoryName:string;
  strProductCategoryRegularPrice:string;

  strProductGuid:string;

  constructor(public navCtrl: NavController, public navParams: NavParams,public modalCtrl: ModalController,
    public httpClient: HttpClient,public loadingController: LoadingController) {

      this.strId = navParams.get('term_id');
      this.dynamicTermId = this.strId;  
   
      // console.log('Received productsList id ' + this.strId);
  }
  
  ionViewDidLoad() {
    console.log('ionViewDidLoad ProductcategorydetailPage');
  }


  searchPage() {
    // let modal = this.modalCtrl.create(SearchPage);
    // modal.present();
  }
    
  cartPage() {
    // let modal = this.modalCtrl.create(CartPage);
    // modal.present();

    this.navCtrl.push(ViewcartPage);
  }

  doRefresh(event) {
    console.log('Begin async operation');
    
    
    setTimeout(() => {
      console.log('Async operation has ended');
      //  event.target.complete();
      event.complete();
      // window.location.reload();
     // window.location.reload();
      // location.reload();
    }, 600);
  }


  sortPopular(){
    this.showLoadingControllerLaunch();
  }

  ngOnInit() {
    this.showLoadingControllerLaunch();
    this.httpClient.get('http://busybanda.com/sterling-tools/api/get_category_by_id?' +  'id=' +this.dynamicTermId)
    .subscribe((jsonResponse) => {
      
      this.productCategoryInformation = jsonResponse['result'];

      this.obj = JSON.stringify(jsonResponse);

        // console.log('Particular product details ' + this.obj);

    

      console.log('Product category length ' + this.productCategoryInformation.length);



    //   for (var key in this.productCategoryInformation) {
    //     if (this.productCategoryInformation.hasOwnProperty(key)) {
    //          // alert(this.productCategoryInformation[key].id);

    //           console.log('Product category Id ' + this.productCategoryInformation[key].id);

    //           this.strProductCategoryName = 'Name: ' + this.productCategoryInformation[key].id.name;
    //     }
    //  } 

  







      
      //  if (this.productCategoryInformation && this.productCategoryInformation.length) {
      //   console.log('Particular product details available ' );
          
      //  }
      // else 
      // {
      //   this.strData = 'No data available';
      //   console.log('Particular product empty ' + jsonResponse['result']);
      // }

      for (const entry of this.productCategoryInformation) {
       
        this.strProductCategoryName = 'Name: ' + entry.name;
        // this.strIdValue = 'Id: ' + entry.ID;
        // this.strCommentStatus = 'Comment Status: ' + entry.comment_status;
        // this.strPingStatus = 'Ping Status: ' + entry.ping_status;
       
        // this.strProductGuid = 'Product Guid: ' + entry.guid;
        // this.strProductCategoryRegularPrice = 'Price: ' + entry.regular_price;

         // console.log('Name:  ' + entry.name);

      }

      for (const entry of this.productCategoryInformation) {

        //console.log(key); // key1 and etc...
         console.log(entry.name); // val1 and etc...
         // console.log('Name:  ' + key.name);
        // this.strProductCategoryName = 'Name: ' + entry.name;
      }

        
      
      
       

    });
  } 


  showLoadingControllerLaunch() {
    let loading = this.loadingController.create({
      content: 'Please wait!'
    });
  
    loading.present();
    // this.callRegisterApi();
  
    setTimeout(() => {
      loading.dismiss();
    }, 3000);
  }



}



